.right-aside {
  grid-column: 2 span;

  @include tablet {
    padding-bottom: 12rem;
  }

  &__title {
    display: block;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 1.2rem;
    line-height: 150%;
    color: #fff;

    border-radius: .8rem .8rem 0 0;
    padding: 0.5rem 1rem;
    margin-bottom: 0.5rem;
    background: var(--burgundy);
  }


  &__capture {
    margin: 0;
    margin-bottom: .7rem;
    text-align: center;

    @include tablet {
      max-width: 16.1rem;
      margin: 0 auto;
    }
  }

  &__text {
    margin: 0;
    margin: 1.2rem 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 117%;
    color: #5a5a5a;

    &:not(:last-child) {
      margin-bottom: 1.8rem;
    }

    @include tablet {
      max-width: 50rem;
      margin: 0 auto;
      margin-bottom: 3.2rem;
    }
  }

  &__licens {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.8rem;
    padding: 0 3.5rem;

    &:not(:last-child) {
      margin-bottom: 1.8rem;
    }

    img {
      display: block;
      width: 10.9rem;
      height: auto;
      object-fit: contain;
      cursor: pointer;
      transition: .3s ease-out;

      @include hover {
        transform: translateY(-4px);
      }
    }
  }

  &__link {
    margin: 1.5rem 0;
    width: 100%;
    text-align: center;
    border: 1px solid var(--burgundy);
    border-radius: 3px;
    background-color: #fff;
    display: inline-flex;
    justify-content: center;
    padding: 0.6rem 1rem;
    text-align: center;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 133%;
    text-align: center;
    color: var(--burgundy);
    transition: .3s ease-out;

    @include hover {
      background-color: rgba(#671414, 0.3);
    }

    @include tablet {
      display: block;
    }
  }

  &__block {
    &:not(:last-child) {
      margin-bottom: 1rem;
    }
  }

  &__extra {
    ul {
      padding: 0;
      margin: 0;
      list-style: none;
    }

    ul>li {
      display: flex;

      &::before {
        content: "";
        display: inline-flex;
        margin-top: .6rem;
        vertical-align: center;
        margin-right: 1rem;
        width: 0.3rem;
        height: 0.3rem;
        flex-shrink: 0;
        border-radius: 100%;
        background-color: #671414;
      }

      padding: 1rem 1.5rem;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 1.2rem;
      line-height: 1.15;
      color: var(--burgundy);
      border-bottom: 1px solid #ececec;
      background: #fefefe;
      transition: .3s ease-out;

      @include hover {
        color: #a55050;
      }

      &>a {
        color: inherit;
        text-decoration: underline;
      }
    }
  }

  &__testi {
    padding: 1.5rem;
    border: 1px solid #d7d7d7;
    position: relative;
    margin-bottom: 2.4rem;

    &:not(:last-child) {
      margin-bottom: 3rem;
    }

    p {
      margin: 0;
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 1rem;
      line-height: 140%;
      color: #5a5a5a;
    }

    &::after {
      content: "";
      position: absolute;
      top: 100%;
      left: 2rem;
      width: 3.4rem;
      height: 1.4rem;
      background-image: url('./../img/angle.webp');
      background-size: contain;
      background-position: top;
      background-repeat: no-repeat;
    }
  }

  &__consult {
    display: block;
    margin: 2rem 0 1.4rem;

    @include tablet {
      width: 100%;
      max-width: 18rem;
      margin: 0 auto;
    }

    img {
      width: 100%;
      height: auto;
      object-fit: contain;
    }
  }

  &__n {
    margin-bottom: 1.6rem;
    max-width: 34.4rem;
    margin: 0 auto;

    @include tablet {
      max-width: 34.4rem;
    }


  }

  &__otz {
    display: block;
    margin-bottom: 1.8rem;

    @include tablet {
      width: 100%;
      max-width: 18rem;
      margin: 0 auto;
    }

    img {
      width: 100%;
      height: auto;
      object-fit: contain;
    }
  }

  &__dzen {
    display: block;
    margin-top: 2rem;

    @include tablet {
      width: 100%;
      max-width: 18rem;
      margin: 0 auto;
      margin-top: 2.3rem;
    }

    img {
      width: 100%;
      height: auto;
      object-fit: contain;
      cursor: pointer;
    }
  }
}

.ra-news {
  display: block;

  &:not(:last-child) {
    margin-bottom: 1.6rem;
  }

  &__date {
    border-radius: 3px;
    display: inline-block;
    margin-bottom: 0.6rem;
    background: #783030;
    padding: .1rem 1.4rem;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 1rem;
    line-height: 220%;
    text-align: center;
    color: #fff;
  }

  &__text {

    display: block;
    margin: 0 1.2rem;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 1rem;
    line-height: 140%;
    color: #4f4e4e;

  }
}